                        //     imo: 308275000,
                        //     startTime: "2018-03-25",
                        //     endTime: "2018-03-31"
                        //   };
                        //      obj1.imo = imo;
                        //      obj1.startTime = startTime;
                        //      obj1.endTime = endTime;
                        console.log(JSON.stringify(obj1));
                        $.ajax({
                            url: self.url + "/api/geojson/waypointnew.json",
                            type: "post",
                            data: JSON.stringify(obj1),
                            dataType: "json",
                            contentType: "application/json",
                            success: function(data) {
                                console.log(data, 'data');
                                if(data.length != 0){
                                    var locus = data;
                                    var cog = locus[locus.length - 1].cog; //角度

                                    Maps.setZoom(5); //设置地图层级

                                    ShipLine(Maps, locus, cog); //划线
                                    var symbolEnd13 = {
