                                    var markers1 = []; //文字聚焦数组
                                    for (var i = 0; i < locus.length; i++) {
                                        var c = new google.maps.Marker({
                                            position: { lat: locus[i].lat, lng: locus[i].lng },
                                            map: Maps,
                                            icon: symbolCircle
                                        });

