                                                strokeOpacity: 1,
                                                strokeWeight: 2,
                                                anchor: new google.maps.Point(0, 0),
                                                scale: 1
                                            }
                                        });
                                        markers1.push(marker46);
                                    }
                                    var markerCluster = new MarkerClusterer(Maps, markers);
                                    console.log(markerCluster);
                                    var markerCluster1 = new MarkerClusterer(Maps, markers1);
                                }

