                            type: "post",
                            data: JSON.stringify(obj1),
                            dataType: "json",
                            contentType: "application/json",
                            success: function(data) {
                                var rotation = data.features[0].properties.rotation;
                                var latLng = {lat: data.features[0].properties.lat, lng: data.features[0].properties.lng};

                                var shipTypeShow = {
                                    path: "M-6 10L0 -10L6 10z",
                                    strokeWeight: 2,
                                    strokeColor: "#ff4600",
                                    fillColor: "#00de1a",
                                    fillOpacity: 1,
                                    scale: 1,
                                    rotation,
                                };
                                let shipMar = new google.maps.Marker({
                                    position: latLng,
                                    map: Maps,
                                    icon: shipTypeShow
                                });

                                Maps.setCenter(latLng);

                                Maps.setZoom(5);

                            }
