</script>

<style scoped>
	html,
	body {
		margin: 0;
		padding: 0;
		height: 100%;
		width: 100%;
	}

	#map {
		height: 100vh;
		width: 100%;
	}

	.ssss {
		background-color: red;
		width: 1em;
		height: 1em;
		margin-bottom: 10px;
	}
</style>



